<script lang="ts">
    import { onMount } from "svelte"
    import { base } from "$app/paths"
    import { Application, Container } from "pixi.js"
    import type { Renderer } from "pixi.js"

    /**
     * https://codepen.io/JuanFuentes/pen/jONVOOL
     * https://gsap.com/community/forums/topic/39641-liquify-effect-with-pixijs-and-gsap/
     * https://github.com/KokoDoko/piximatters
     * https://experiments.withgoogle.com/nebulon
     * https://pixijs.com/header/
     * https://github.com/schteppe/p2.js
     * https://github.com/GoodBoyDigital/phaser
     * https://charlottedann.com/article/soft-blob-physics
     * https://github.com/gorhill/Javascript-Voronoi
     * https://www.reddit.com/r/Unity2D/comments/njube1/blob_physics_in_a_2d_game/
     * https://www.youtube.com/watch?v=KEMZR3unWTE
     * https://github.com/textura-agency/liquid-threejs-ball
     * https://github.com/mark-gerarts/nature-of-code
     */
    type Rain = { x: number, y: number, z: number, w: number }

    let rainContainer: HTMLElement
    let rainCount = 300
    const app = new Application()
    const rain: Rain[] = []

    async function setup() {
        await app.init({
            width: rainContainer?.clientWidth ?? window.innerWidth,
            height: rainContainer?.clientHeight ?? window.innerHeight,
            resizeTo: rainContainer ?? window,
            antialias: true
        })
        rainContainer.appendChild(app.canvas)
    }

    async function preload() {

    }

    function addRain(app: Application<Renderer>, rain: Rain[]) {
        const container = new Container()
        app.stage.addChild(container)

        for (let i = 0; i < rainCount; i++) {
            rain.push({
                x: Math.floor((Math.random() * document.documentElement.clientWidth) + 9),
                y: Math.floor((Math.random() * rainContainer.clientHeight) + 9),
                z: Math.floor((Math.random() * 2) + 1),
                w: Math.floor((Math.random() * 3) + 2)
            })
        }
    }

    onMount(async () => {
        await setup()
    })
</script>

<div bind:this={rainContainer} class="rain-container fixed inset-0 -z-10 pointer-events-none">

</div>

<div class="fixed top-6 z-10 right-10 md:right-20 flex items-center gap-2">
    <span class="dialouge crimson-text-regular text-sm"></span>
    <button type="button" class="outline-none hover:opacity-100 focus:opacity-100 transition-opacity">
        <img src="{base}/img/character/sonetto.webp" alt="Vertin" class="w-10 h-10 rounded-full overflow-hidden pointer-events-none">
    </button>
</div>
